.loader {
  width: 80px;
  height: 80px;
  transform: translateX(-50%);
  transform: translateZ(50%);
  transform-origin: 40px 40px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotate(45deg);
}

.loader div {
  width: 32px;
  height: 32px;
  background: #ff6b01;
  animation: loader 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: absolute;
  top: 32px;
  left: 32px;
}

.loader div:after, .loader div:before {
  content: " ";
  width: 32px;
  height: 32px;
  background: #ff6b01;
  display: block;
  position: absolute;
}

.loader div:before {
  border-radius: 50% 0 0 50%;
  left: -24px;
}

.loader div:after {
  border-radius: 50% 50% 0 0;
  top: -24px;
}

@keyframes loader {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

